.App {
  position: relative;
  top: 0;
  right: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #eff6fc;
  overflow-x: hidden;
  overflow: overlay;
  color: #424242;
}

.App-content {
  position: relative;
  z-index: 1;
  padding-top: 3em;
  width: 100%;
  height: auto;
  min-height: 100%;
  overflow-x: hidden;
}

h1{
  color:purple;
}

/* width */
::-webkit-scrollbar {
  width: .4em;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}