.media{
    align-self: center;
    justify-content: flex-end;
    display: flex;
    flex: 2;
}

.fa {
    padding: .5em;
    font-size: 1.2em;
    width: 2em;
    text-align: center;
    text-decoration: none;
    margin: 5px;
}

.fa:hover {
    opacity: 0.7;
}

.fa-facebook {
    background: #3B5998;
    color: white;
}

.fa-twitter {
    background: #55ACEE;
    color: white;
}

.fa-linkedin {
    background: #007bb5;
    color: white;
}

.fa-github {
    background: #211F1F;
    color: white;
}