.toggle{
    display: flex;
    background-color: transparent;
    justify-content: flex-start;
}

.toggle div{
    background-color: white;
    text-align: center;
    height: 100%;
    width: 7em;
    padding: .5em 0 .5em 0;
    box-shadow: 0.2em 0.2em .5em #d7eafa;
}

.active-btn{
    background-color:#ebf5ff !important;
    color: purple;
    box-shadow: 
        inset .2em .2em .5em #d0e5f7 !important;
}
.toggle div:hover{
    cursor: pointer;
    color: #a102a1;
}

.toggle div:active{
    background-color: #deecf9;
 }