.home{
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 92vh;
}

.righth{
    display: flex;
    height: 100%;
    margin-inline-start: none;
    margin-inline-end: none;
}

.lefth{
    height: 100%;
    width: 50%;
    text-align: left;
    display: flex;
    flex-direction: column;
    padding: 5vw;
    justify-content: center;
}

.gray{
    text-align: left;
    color: #424242;
    display: inline;
    font-weight: normal;
}

.iam{
    position: relative;
    display: flex;
    flex-flow: row;
    justify-content: left;
}

.description{
    width: 35vw;
    padding-top: 1vw;
    margin-inline-start: none;
    margin-inline-end: none;
}